import React, { Component } from 'react';
import './index.scss'
import classnames from 'classnames'
import menu from '../../App/images/menu.png'
class Aside extends Component {
    constructor(props) {
        super(props);
        this.state = {
            show: false
        }
    }
    handleCLick=(e)=> {
        const {key } =e.target.dataset
        const {onChange} = this.props
        onChange&&onChange(key)
    }
    render() {
        const {show,active} = this.props
        return (<aside onClick={this.handleCLick} className={classnames("aside-nav", {
            'isShow': show
        })}>
            <dir className='aside-head'>
                <img src={menu} alt='img'/>
            </dir>
            <ul>
                <li data-key='home' className={classnames({'active': active === 'home'})}>Home</li>
                <li data-key='pixure' className={classnames({'active': active === 'pixure'})}>Pixure</li>
                <li data-key='size-finder' className={classnames({'active': active === 'size-finder'})}>Size Finder</li>
                <li data-key='w-wardrobe' className={classnames({'active': active === 'w-wardrobe'})}>W Wordrobe</li>
                <li data-key='we-pattern' className={classnames({'active': active === 'we-pattern'})}>We Pattern</li>
                <li data-key='developer' className={classnames({'active': active === 'developer'})}>Developer</li>
                <li data-key='join-us' className={classnames({'active': active === 'join-us'})}>Join us</li>
            </ul>
        </aside>);
    }
}

export default Aside;